<template>
  <form @submit.prevent="addMessage()">
      <div class="input-field btn-group">
       <i class="material-icons prefix">forum</i>
       <input type="text" placeholder="Your Message..." v-model="text">
       <input type="button" @click="deleteData()" value="Delete Your Messages" class="btn red lighten-2">
     </div>
  </form>
</template>

<script>

import db from '../firebase/init.js'
import util from '../utils/util'

export default { 
    props:['name' , 'content'],
    data(){
        return{
            text:null,
        }
    },
    methods:{
        addMessage(){
    
            let date    = new Date(),
                hours   = date.getHours(),
                minutes = date.getMinutes(),
                seconds = date.getSeconds();

            let time = `${hours}:${minutes < 10 ? '0' + minutes : minutes}:${seconds < 10 ? '0' + seconds : seconds}
                        ${hours > 12 ? 'PM' : 'AM'}`;
            
            let _obj  = {
                text:this.text,
                name:this.name,
                createdAt:time
            }
            
            if(this.text){
                db.collection('chats').add(_obj);
                util.scrollBottom(this.content);
                this.text = '';
            }
            else
                util.Handler('Field must not be empty!');
            
        },
        deleteData(){

          let delete_message = db.collection('chats').where('name','==', this.name);
     

          delete_message.get().then(function(querySnapshot) {
              
            querySnapshot.forEach(function(doc) {
                doc.ref.delete();
                 return util.Handler('Your messages was deleted !' , false);
            });

          });
      
            
        }
    }
    
}
</script>

<style>
::-webkit-scrollbar {
    width: 10px;
}
 
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px rgba(0,0,0,0.3); 
    border-radius: 10px;
}
 
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background:#d1c4e9; 
}

</style>



