<template>
  <div class="container" id="app">
    <h4 class="center  green-text text-darken-1">Welcome</h4>
    <hr>
    <form @submit.prevent="startChat()">
          <div class="input-field">
            <i class="material-icons prefix">account_circle</i>
            <input type="text" class="icon_prefix" placeholder="Your name..." name="name"
            v-model="name"
            >
          </div>
         <input type="submit" value="Ready" class="btn green lighten-1">
         <input v-if="this.name ? this.name.toLowerCase() == 'robert' : '' " 
                type="button" value="Delete Data" class="btn red lighten-1"
                @click="deleteAllData()"
                >
         
    </form>
  </div>
</template>

<script>
import db from '../firebase/init';
import util from '../utils/util'

export default {
  name:'app',
  data(){
    return{
      name:null,
    }
  },
  firestore(){
    return{
      chats:db.collection('chats')
    }
  },
  methods:{
    startChat(){
      if(this.name){
        util.setCookie(this.name);
        this.$router.push({ name:'chat' , params:{ name:this.name } });
      }
      else
        util.Handler('Enter your name before start chating');
      
        
    },
    deleteAllData(){

         if(this.chats.length <= 0)
            util.Handler('Database is alredy empty');
         else{
            this.chats.map((el) => {
                  db.collection('chats').doc(el['.key']).delete();   
            });
            return util.Handler('Data was deleted !' , false)
         }
    },
  },
}
</script>

<style  scoped>

.input-field:hover .prefix{
  transition: .3s all;
  transform:translateY(10%);
  color: #26a69a;
}

#app{
  margin: calc(20% - 10%) auto;
  max-width: 500px;

}
  input[type="submit"]{width: 70%;}
</style>



